import { Logger } from '@nestjs/common'
import { inject, injectable } from 'inversify'
import { COSSymbol } from '@flowda-projects/flowda-shared'
import type * as COS from 'cos-nodejs-sdk-v5'

@injectable()
export class AssetsService {
  private readonly logger = new Logger(AssetsService.name)

  constructor(@inject(COSSymbol) private cos: COS) {}

  public async upload(fileData: any) {
    const {
      fieldname,
      originalname,
      encoding, // '7bit',
      mimetype, // 'image/svg+xml',
      buffer,
      size,
    } = fileData
    console.log('upload got: ', fileData, typeof buffer)
    // SECRETID 和 SECRETKEY请登录 https://console.cloud.tencent.com/cam/capi 进行查看和管理
    return new Promise((resolve, reject) => {
      const format = originalname?.split('.').pop() || 'png'

      // if (typeof buffer !== 'arraybuffer') {
      //   reject('invalid data type')
      // }
      this.cos.putObject(
        {
          Bucket: 'common-1306445775' /* 必须 */,
          Region: 'ap-shanghai' /* 必须 */,
          Key: `attachment/img-upload-${Math.floor(new Date().valueOf() / 1000)}.${format}`, // 这里暂且限制最多每秒一个，重复了强制覆盖
          StorageClass: 'STANDARD',
          Body: buffer, // 上传文件对象
          ContentType: mimetype || 'text/html', // 默认应该都是html格式
          onProgress: function (progressData) {
            console.log(JSON.stringify(progressData))
          },
        },
        (err: any, data: any) => {
          console.log(`upload to tencent with resp:  ${JSON.stringify(err || data)}`)
          if (err) {
            reject(err)
          } else {
            resolve(data?.Location as string)
          }
        },
      )
    })
  }
}
