import { inject, injectable } from 'inversify'
import type { SchemaService } from '@flowda-projects/flowda-shared'
import { FlowdaTrpcClientSymbol, SchemaServiceSymbol } from '@flowda-projects/flowda-shared'
import type { AppRouter } from '@flowda-projects/flowda-services-trpc-server'
import type { CreateTRPCProxyClient } from '@trpc/client'

@injectable()
export class CmsAdminSchemaService {
  constructor(
    @inject(SchemaServiceSymbol) private schema: SchemaService,
    @inject(FlowdaTrpcClientSymbol) private flowdaTrpc: CreateTRPCProxyClient<AppRouter>,
  ) {}

  async getSchema() {
    const schema = await this.flowdaTrpc.schema.getSchema.query()
    const schema2 = this.schema.getSchema()
    const ret = Object.assign({}, schema, schema2)
    return ret
  }
}
