import { z } from 'zod';
import type { Prisma } from '@prisma/client-cms_admin';

/////////////////////////////////////////
// HELPER FUNCTIONS
/////////////////////////////////////////


/////////////////////////////////////////
// ENUMS
/////////////////////////////////////////

export const AuditsScalarFieldEnumSchema = z.enum(['id','createdAt','auditId','auditType','userId','username','action','auditChanges','version']);

export const ProductLineScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','name','description']);

export const SortOrderSchema = z.enum(['asc','desc']);

export const TransactionIsolationLevelSchema = z.enum(['ReadUncommitted','ReadCommitted','RepeatableRead','Serializable']);
/////////////////////////////////////////
// MODELS
/////////////////////////////////////////

/////////////////////////////////////////
// PRODUCT LINE SCHEMA
/////////////////////////////////////////

export const ProductLineSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  name: z.string().openapi({"title":"产线名"}),
  description: z.string().openapi({"title":"产线描述","column_type":"textarea"}),
}).openapi({"primary_key":"id","display_name":"产线","display_column":"name","display_primary_key":"false","searchable_columns":"name"})

export type ProductLine = z.infer<typeof ProductLineSchema>

/////////////////////////////////////////
// AUDITS SCHEMA
/////////////////////////////////////////

export const AuditsSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  auditId: z.number().int().openapi({"title":"关联"}),
  auditType: z.string().openapi({"title":"审计类型(关联"}),
  userId: z.string().openapi({"title":"用户"}),
  username: z.string().nullable().openapi({"title":"用户名"}),
  action: z.string().openapi({"title":"动作(e.g."}),
  auditChanges: z.string().openapi({"title":"变化"}),
  version: z.number().int().openapi({"title":"版本"}),
}).openapi({"display_name":"审计日志"})

export type Audits = z.infer<typeof AuditsSchema>
