import { Controller, Get } from '@nestjs/common'
import { CmsAdminSchemaService } from '@flowda-projects/cms-admin-services'

@Controller('/apps')
export class AppController {
  constructor(private readonly cmsAdminSchemaService: CmsAdminSchemaService) {}

  @Get('/__hi')
  async hi() {
    return {
      hi: 'cms-admin-api',
    }
  }

  @Get('/getSchema')
  async getSchema() {
    const schema = await this.cmsAdminSchemaService.getSchema()
    return schema
  }
}
